import React, { Component } from 'react';
import ContactForm from './contactForm/ContactForm';
import Filter from './filter/Filter';
import ContactList from './contactList/ContactList';

export class App extends Component {
  state = {
    contacts: [
      { id: 'id-1', name: 'Rosie Simpson', number: '459-12-56' },
      { id: 'id-2', name: 'Hermione Kline', number: '443-89-12' },
      { id: 'id-3', name: 'Eden Clements', number: '645-17-79' },
      { id: 'id-4', name: 'Annie Copeland', number: '227-91-26' },
    ],
    filter: '',
  };

  addToContacts = objectContact => {
    this.state.contacts.find(contact => contact.name === objectContact.name)
      ? alert(`${objectContact.name} is already in contacts.`)
      : this.setState(prevState => {
          // console.log({
          //   contacts: [...prevState.contacts, objectContact],
          // });
          return {
            contacts: [...prevState.contacts, objectContact],
          };
        });
  };

  findByName = e => {
    const filter = this.state.filter;
    const contacts = this.state.contacts;
    this.setState({ filter: e.target.value });
    return contacts.filter(
      contact => contact.name.toLowerCase() === filter.toLowerCase(),
    );
  };

  render() {
    // const name = this.state.name;
    // const number = this.state.number;
    const filter = this.state.filter;
    const contacts = this.state.contacts;

    return (
      <>
        <h1>Phonebook</h1>
        <ContactForm onSubmit={this.addToContacts} />
        {/* {contacts.length > 0 ? ( */}
        <div>
          <h2>Contacts</h2>
          <Filter valueFilter={this.findByName} />
          <ContactList contacts={contacts} />
          {/* <ContactList contacts={this.findByName()} /> */}
        </div>
        {/* ) : (
          <h2>Your phonebook is empty</h2>
        )} */}
      </>
    );
  }
}

export default App;
